import React from 'react'
import Client1 from "../img/client1.jpg";
import Client2 from "../img/client2.jpg";
import Client3 from "../img/client3.jpg";
import Client4 from "../img/client4.jpg"
function Clients() {
    return (
        <div className="clients">
              
                <h1>Our Clients</h1>
            
            <div className="container">
                <div className="row">
                    <div className="col-3">
                        <div className='client'>
                            <img src={Client1} alt="client" width="250px" height="150px"/>
                        </div>
                    </div>
                    <div className="col-3">
                        <div className='client'>
                        <img src={Client2} alt="client" width="250px" height="150px"/>
                        </div>
                    </div>
                    <div className="col-3">
                        <div className='client'>
                        <img src={Client3} alt="client" width="250px" height="150px"/>
                        </div>
                    </div>
                    <div className="col-3">
                        <div className='client'>
                        <img src={Client4} alt="client" width="250px" height="150px"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Clients;
