import React from "react";

const Shop = () => {
    return(
        <div className="shop" id="shop">
            <div className="container">
                <div className="shop_text">
                    <div>
                        <h1>SPECIAL OFFER  FOR  CLIENT,START FROM $100</h1>
                        <div className="shop__btn">
                            <a to="" className="btn btn-smart">
                            SHOP NOW
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Shop;