import React from 'react'

function Item(props) {
    return (
        
          <div className="price">
           
                <div className="price__img">
                    <img src={props.pimg} alt="pizza"/>
                </div>
                    <h1 className="price__heading"> {props.heading } </h1>
                <p className="price__text">
                     {props.text}
                </p>
               
                <div className="price__btn">
                <a href="#order" className="btn btn-smart"> {props.price}</a>
                </div>
            </div>
      
    )
}

export default Item;
